export default {
	data() {
		return {
			id:'',
			status:0,
			detailId:''
		}
	},
	onLoad(e) {
		// console.log(e)
		this.id = e.id
		this.getData()
	},
	methods: {
		exit(){
			this.$api.remove_club_out({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				id:this.detailId
			}).then(res=>{
				uni.reLaunch({
					url:'../../pagesTwo/Club/Club'
				})
			})
		},
		stay(){
			this.$api.remove_club_stay({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				id:this.detailId
			}).then(res=>{
				// console.log(res)
				uni.reLaunch({
					url:'../../pagesTwo/Club/Club'
				})
			})
		},
		goClub(){
			uni.navigateTo({
				url:'../../pagesTwo/Club/Club'
			})
		},
		getData(){
			this.$api.club_login_out_detail({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				club_id:this.id
			}).then(res=>{
				// console.log(res)
				this.detailId = res.data.data.id
				this.status = res.data.data.status
			})
		}
	}
}
